export default {
	data() {
		return {
			baseUrl: getApp().globalData.baseUrl,
			InputBottom: 0,
			comment: '',
			userId: '',
			videoId: '',
			createrId: '',
			videoDesc: '',
			defaultAvatar: '/static/images/avatar.jpg',
			commentList: [],
			isLoading: false,
		}
	},
	onLoad(params) {
		this.createrId = params.createrId
		this.videoId = params.videoId
		this.videoDesc = params.videoDesc
		this.getAllComments()
	},
	methods: {
		toUserPage(userId) {
			uni.navigateTo({
				url: '../person/person?userId=' + userId
			})
		},
		InputFocus(e) {
			this.InputBottom = e.detail.height
		},
		InputBlur(e) {
			this.InputBottom = 0
		},
		getAllComments() {
			let user = getApp().globalData.getGlobalUserInfo()
			this.userId = user.id
			uni.request({
				url: this.baseUrl + '/video/getVideoComments?videoId=' + this.videoId,
				method: 'POST',
				header: {
					'content-type': 'application/json',
					'userId': user.id,
					'userToken': user.userToken
				},
				success: (res) => {
					// console.log(res);
					if (res.data.status === 200) {
						this.commentList = []
						this.commentList = res.data.data
					}
				}
			})
		},
		// 获取容器高度，使页面滚动到容器底部
		pageScrollToBottom() {
			// 使页面滚动到底部
			uni.createSelectorQuery().select('#page').boundingClientRect(function(rect) {
				// 使页面滚动到底部
				uni.pageScrollTo({
					scrollTop: rect.height
				})
			}).exec()

		},
		submitComment() {
			if (this.comment === '' || this.isLoading) {
				return
			}
			let user = getApp().globalData.getGlobalUserInfo()
			this.isLoading = true
			let data = {
				videoId: this.videoId,
				fromUserId: user.id,
				comment: this.comment
			}
			uni.request({
				url: this.baseUrl + '/video/saveComment',
				method: 'POST',
				header: {
					'content-type': 'application/json',
					'userId': user.id,
					'userToken': user.userToken
				},
				data: data,
				success: (res) => {
					this.isLoading = false
					this.comment = ''
					if (res.data.status === 200) {
						// 重新获取数据
						this.getAllComments()
						setTimeout(() => {
							this.pageScrollToBottom()
						},300)
					}
				}
			})
		}
	}
}
